.App {
  font-family: sans-serif;
  text-align: center;
  background-color: rgb(146, 146, 204);
}
h1 {
  font-size: 75px;
  color: rgb(182, 216, 166);
  margin-bottom: 20px;
}

button {
  background-color: #007bff;
  color: rgb(240, 234, 234);
  padding: 15px 20px;
  border: none;
  border-radius: 29px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #c8accc;
}

.react-image-gallery-thumbnail {
  border: 6px solid rgb(156, 96, 96);
  border-radius: 40px;
}

.react-image-gallery-thumbnail:hover {
  border-color: #3151c4;
}
body {
  margin: 0;
  padding: 0;
  margin-top: 0px;
}
